  align-items: center;
  z-index: 200;
  background: white;
  padding: 32px 16px;
  border-radius: 20px 20px 0 0;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  -webkit-transition: -webkit-transform 0.3s ease-out;
  transition: -webkit-transform 0.3s ease-out;
