.Burger {
  width: 100%;
  margin: auto;
  /* margin-bottom: 50px; */
  height: 300px;
  overflow-Y: scroll;
  -webkit-overflow-scrolling: touch;
  text-align: center;
  font-weight: bold;
  font-size: 1.2rem;
}

@media (min-width: 500px) and (min-height: 400px)
{
  .Burger {
    width: 350px;
    height: 300px;
  }
}

@media (min-width: 500px) /*and (min-height: 401px)*/
{
  .Burger {
    width: 450px;
    height: 400px;
  }
}

@media (min-width: 1000px) and (min-height: 700px)
{
  .Burger {
    width: 700px;
    height: 500px;
  }
}


